import React from "react";
import { Modal, Button } from 'react-bootstrap';
import { getCurrentTime } from "./Interact";

export const ModalCreate = (props) => {
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            keyboard={false}
            backdrop="static"
            centered
        >
            <Modal.Body>
                <h4 style={{color: 'black'}}>xito!! </h4>
                <div id="form-div">
                    <form id="form-token" method="post" action="">
                        <div id="div-form-input-container">
                            <input
                            type="text"
                            name="idToken"
                            readOnly="readOnly"
                            value={props.token[1]}
                            placeholder='ID-Token'
                            />
                            <input
                            type="text"
                            name="owner"
                            readOnly="readOnly"
                            value={props.token[0]}
                            placeholder='Cuenta'
                            />
                            <input
                            type="text"
                            name="code"
                            readOnly="readOnly"
                            value={props.token[2]}
                            placeholder='C贸digo'
                            />
                            <input
                            type="text"
                            name="timestamp"
                            readOnly="readOnly"
                            value={getCurrentTime(props.token[3])}
                            placeholder='Fecha de creaci贸n'
                            />
                            <input
                            type="text"
                            name="hash"
                            readOnly="readOnly"
                            value={props.hash}
                            placeholder='Hash de transacci贸n'
                            />
                            <input
                            type="text"
                            name="idContract"
                            readOnly="readOnly"
                            value={props.contract}
                            style={{display: "none"}}
                            />
                        </div>
                        <div id="div-form-text-container">
                            <p>{props.message}</p>
                            <div id="div-load">
                                <h1><i className="fab fa-ethereum" aria-hidden="true" style={{fontSize: "1.7em", marginBottom: "0.6em", color: "black"}}/> <br></br></h1>
                            </div>
                            <button id='formButton' type="submit">OK</button>
                        </div>
                    </form>
                </div>
            </Modal.Body>
        </Modal>
    );
}

export const ModalEmpty = (props) => {
    return (
        <Modal
            {...props}
            size="md"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Body>
                <h4>Oops!!</h4>
                <p>
                    {props.message}
                </p>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={props.onHide}>Cerrar</Button>
            </Modal.Footer>
        </Modal>
    );
}

export const ModalWallet = (props) => {
    return (
        <Modal
            {...props}
            size="md"
            aria-labelledby="contained-modal-title-vcenter"
            keyboard={false}
            backdrop="static"
            centered
        >
            <Modal.Body>
                <h4>MetaMask desconectado</h4>
                <p>
                    {props.message}
                </p>
                <p>Puedes descargar MetaMask <a
                    target="blank"
                    href="https://metamask.io/download.html"
                    onClick={props.onHide}>aqu铆 </a>
                    si no lo tienes instalado en tu navegador</p>
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={props.onHide}>Cerrar</Button>
            </Modal.Footer>
        </Modal>
    );
}

