import React from "react";
import { useEffect, useState } from "react";
import { createToken, lastToken, connectWallet, getCurrentWalletConnected } from "./model/Interact.js";
import { LastTokenTable} from "./model/EventTable.js";
import LogoSoferim from "./model/LogoSoferim.png"
import { ModalCreate, ModalEmpty, ModalWallet, ModalReject, ModalAlreadyExist, ModalLoad } from "./model/Modal.js";
import { HelpSection } from "./model/Help.js"
import Messages from "./model/Messages.json";


const App = () => {
  //state variables
  const setInitialState = [];
  const [currentToken, setToken] = useState("");
  const [idToken, setIdToken] = useState(setInitialState);
  const [modalShow, setModalShow] = useState(false);
  const [modalEmptyShow, setModalEmptyShow] = useState(false);
  const [modalWalletShow, setModalWalletShow] = useState(false);
  const [modalLoadShow, setModalLoadShow] = useState(false)
  const [modalReject, setModalReject] = useState(false);
  const [walletAddress, setWallet] = useState("");
  const [status, setStatus] = useState("");
  const [hash, setHash] = useState("");
  const [alreadyShow, setModalAlreadyExist] = useState(false)
  const [idContract, setIdContract] = useState("")

  //called only once
  useEffect(async () => {
    const { address, status } = await getCurrentWalletConnected();
    setWallet(address);
    setStatus(status);
    addWalletListener();
    updateData();
    // ELIMINAR!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    //document.getElementById("idInput").value = 1;
    //!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
    setIdToken(document.getElementById("idInput").value);
    document.getElementById("inputHash").value = '';
    setIdContract(document.getElementById("idContract").value);
  }, []);

  const addWalletListener = () => {
    if (window.ethereum) {
      window.ethereum.on("accountsChanged", (accounts) => {
        if (accounts.length > 0) {
          setWallet(accounts[0]);
          setStatus(" Genera un nuevo token!.");
        } else {
          setWallet("");
          setStatus(" Conecta tu billetera de MetaMask con el bot贸n 'Conectar'.");
        }
      });
    } else {
      setStatus(
        <p>
          {" "}
          {" "}
          <a href={`https://metamask.io/download.html`}>
            Debes intalar MetaMask en tu navegador para poder crear un token.
          </a>
        </p>
      );
    }
  }

  const updateData = async () => {
    const lastTokenCreated = await lastToken();
    setToken(lastTokenCreated.lastTokenCreated);
    return lastTokenCreated.data
  }

  const onCreatePressed = async () => {
    if (isNaN(idToken) || idToken == "") {
      setModalEmptyShow(true)
    } else {
      const createdToken = await createToken(walletAddress, Number.parseInt(idToken));
      if (createdToken.wallet == true) {
        setModalWalletShow(true);
      } else if (createdToken.exist == true) {
        setModalAlreadyExist(true);
      } else if (createdToken == "") {
        setModalReject(true);
      } else if (createdToken.success == true) {
        setHash(createdToken.txHash);
        document.getElementById('inputHash').value = createdToken.txHash
        setModalLoadShow(true);
        setTimeout(async () => {
          const last = await updateData();
          //setData(last);
          setModalLoadShow(false);
          setModalShow(true)
        }, 15000)
      }
    }
  }

  const connectWalletPressed = async () => {
    const walletResponse = await connectWallet();
    if (walletResponse == 0) {
      setModalWalletShow(true);
      setStatus(walletResponse.status);
    } else {
      setStatus(walletResponse.status);
      setWallet(walletResponse.address);
    }
  }

  //the UI of our component
  return (
    <div id="root">
      <div id="container">
        <div id="header">
          <img id="logo" src={LogoSoferim} alt="Soferim logo"></img>
          <button id="walletButton" onClick={connectWalletPressed}>
            {walletAddress.length > 0 ? (
              "Connected: " +
              String(walletAddress).substring(0, 6) +
              "..." +
              String(walletAddress).substring(38)
            ) : (
              <span>Conectar billetera</span>)}
          </button>
        </div>
            <input id="inputHash" type="hidden"/>
            <LastTokenTable currentToken={currentToken} /><h3 style={{ paddingTop: "18px" }}>ID de tu nuevo Token</h3><div>
              <table id="buttonsTable">
                <tbody>
                  <tr>
                    <td>
                      <input
                          id="idInput"
                          defaultValue="C贸digo de Token"
                          readOnly='readOnly'
                        />
                    </td>
                    <td>
                      <button
                        id="publish"
                        onClick={onCreatePressed}>
                        Generar
                      </button>
                    </td>
                    <input type="hidden" id="idContract" value="1"></input>
                    <ModalEmpty
                      show={modalEmptyShow}
                      onHide={() => setModalEmptyShow(false)}
                      message={Messages.data.empty.message} />
                    <ModalWallet
                      show={modalWalletShow}
                      onHide={() => setModalWalletShow(false)}
                      message={Messages.data.metaNotInstalled.message} />
                    <ModalReject
                      show={modalReject}
                      onHide={() => setModalReject(false)}
                      message={Messages.data.rejected.message} />
                    <ModalCreate
                      show={modalShow}
                      onHide={setModalShow}
                      hash={hash}
                      token={currentToken}
                      message={Messages.data.success.message}
                      contract={idContract} />
                    <ModalAlreadyExist
                      show={alreadyShow}
                      onHide={() => setModalAlreadyExist(false)}
                      message={Messages.data.exists.message} />
                    <ModalLoad
                      show={modalLoadShow}
                      onHide={() => setModalLoadShow(false)}
                      message={Messages.data.load.message} />
                  </tr>
                </tbody>
              </table>
            </div>
      </div>
      <div id="helpSection">
        <HelpSection id="helpSection" />
      </div>
    </div>

  )
}

export { App }
