require('dotenv').config()

const Web3 = require("web3");
const rpcURL = `https://rinkeby.infura.io/v3/${process.env.REACT_APP_URL_INFURA_KEY}`;
const web3 = new Web3(rpcURL);

const contractABI = require("../contract-abi.json");
const contractAddress = process.env.REACT_APP_CONTRACT_ADDRESS;

const myContract = new web3.eth.Contract(contractABI, contractAddress);

export const lastToken = async () => {
    let data = [];
    let lastTokenCreated = await myContract.methods.getInfo().call()
    data[0] = lastTokenCreated.account
    data[1] = lastTokenCreated.tokenId
    data[2] = lastTokenCreated.code256
    data[3] = lastTokenCreated.date
    return {data, lastTokenCreated}
};

export const createToken = async (walletAddress, tokenID) => {
    const ext = exists(tokenID)
    if (!walletAddress) {
        return{wallet: true}
    }else if((await ext).tkExist == true){
        return {exist : true};
    }
    const transactionParameters = {
        to: contractAddress,
        from: walletAddress,
        data: myContract.methods.safeMint(walletAddress, tokenID).encodeABI()
    }
    //sign the transaction
    try {
        const txHash = await window.ethereum.request({
            method: "eth_sendTransaction",
            params: [transactionParameters],
        });
        return {txHash, success: true};
    } catch (error) {
        return "";
    }
}

export const getCurrentTime = (timestamp) => {
    const d = new Date(timestamp * 1000);

    const timeStampCon = d.toLocaleDateString() + "    " + d.toLocaleTimeString();
    return timeStampCon;
};

export const connectWallet = async () => {
    if (window.ethereum) {
        try {
            const addressArray = await window.ethereum.request({
                method: "eth_requestAccounts",
            });
            const obj = {
                status: "Wallet Connected!!",
                address: addressArray[0],
            };
            return obj;
        } catch (err) {
            return {
                address: "",
                status: " " + err.message,
            };
        }
    } else {
        return 0
    };
}

export const getCurrentWalletConnected = async () => {
    if (window.ethereum) {
        try {
            const addressArray = await window.ethereum.request({
                method: "eth_accounts",
            });
            if (addressArray.length > 0) {
                return {
                    address: addressArray[0],
                    status: "Billetera conectada",
                };
            } else {
                return {
                    address: "",
                    status: "Conecta tu billetera usando el boton 'Conectar billetera'.",
                };
            }
        } catch (err) {
            return {
                address: "",
                status: " " + err.message,
            };
        }
    } else {
        return {
            address: "",
            status: (
                <span>
                    <p>
                        {" "}
                        {" "}
                        <a target="_blank" rel="noreferrer" href={`https://metamask.io/download.html`}>
                            Debe instalar MetaMask para poder operar en la plataforma.
                        </a>
                    </p>
                </span>
            ),
        };
    }
};

export const exists = async (idToken) => {
    const tkExist = await myContract.methods.exists(idToken).call()
    if (tkExist == true){
    return {tkExist : true}
    } else {
        return {tkExist : false}
    }
}

