import React from "react";
import { getCurrentTime } from "./Interact";

export const LastTokenTable = (props) => {
    return (
        <React.Fragment>
            <div>
                <h2>Token m谩s reciente </h2>
                <table id="lastTokenTable">
                    <tbody>
                        {props.currentToken ? (
                            <React.Fragment>
                                <tr>
                                    <th>Cuenta</th>
                                    <th>ID-Token</th>
                                    <th>C贸digo</th>
                                    <th>Fecha de creaci贸n</th>
                                </tr>
                                <tr>
                                    <td id="owner">{props.currentToken[0].substring(0, 6) + "..." + props.currentToken[0].substring(38)}</td>
                                    <td id="tokenId">{props.currentToken[1].slice(0, 15)}</td>
                                    <td id="code">{props.currentToken[2].slice(0, 15)}</td>
                                    <td id="timestamp">{getCurrentTime(props.currentToken[3])}</td>
                                </tr>
                            </React.Fragment>
                        )
                            : (<React.Fragment>
                                <tr>
                                    <td>No hay tokens que mostrar</td>
                                </tr>
                            </React.Fragment>
                            )}
                    </tbody>
                </table>
            </div>
        </React.Fragment>
    )
}
